@import './orion-fusion-theme/IconDeviceCheck.module';
@import './clinical-lowlight-theme/IconDeviceCheck.module';

/* stylelint-disable selector-class-pattern */
:local {
  .IconDeviceCheck {
    path:nth-of-type(1) {
      fill: var(--terra-icon-device-check-path-1-fill, currentColor);
    }

    path:nth-of-type(2) {
      fill: var(--terra-icon-device-check-path-2-fill, #78c346);
    }

    path:nth-of-type(3) {
      fill: var(--terra-icon-device-check-path-3-fill, #fff);
    }
  }
}
/* stylelint-enable selector-class-pattern */
